<template>
  <div class="dialog">
    <div class="dialog-cover"
         @click="closeMyself()"
         v-if="isShow"></div>
    <transition name="drop">
      <div class="dialog-content"
           v-if="isShow">
        <span class="dialog-close"
              @click="closeMyself()">×</span>
        <div style="clear:both;"></div>
        <slot>empty</slot>
      </div>
    </transition>
  </div>
</template>

<script type="text/javascript">
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {

    }
  },
  methods: {
    closeMyself () {
      this.$emit('on-close')
    }
  }
}
</script>

<style scoped>
.dialog-cover {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  background-color: rgba(00, 00, 00, 0.5);
}
.dialog-content {
  position: fixed;
  left: 25%;
  top: 15%;
  width: 50%;
  background-color: #fff;
  box-shadow: 0px 0px 1px 1px rgb(78, 78, 78);
  padding: 10px;
  z-index: 5;
}
.dialog-close {
  position: absolute;
  right: 10px;
  top: 0px;
  cursor: pointer;
  font-size: 32px;
}
.drop-enter {
  transform: translateY(-500px);
}
.drop-leave-to {
  transform: translateY(-500px);
}
.drop-enter-active {
  transition: all 0.5s ease;
}
.drop-leave-active {
  transition: all 0.3s ease;
}
</style>



