<template>
  <div class="app">
    <div class="app-header">
      <img src="../assets/logo.png"
           alt=""
           class="logo">
      <ul class="app-headerW">
        <li v-if="username!==''"
            class="user">用户：{{ username }}</li>
        <li class="nav-pile"
            v-if="username!==''">|</li>
        <li v-if="username!==''"
            @click="quit">退出</li>
        <li @click="logDialog()"
            v-if="username===''">登录</li>
        <li class="nav-pile"
            v-if="username===''">|</li>
        <li @click="regDialog()"
            v-if="username===''">注册</li>
        <li class="nav-pile">|</li>
        <li @click="aboutDialog()">关于</li>
      </ul>
    </div>

    <div class="app-content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>

    <div class="app-footer">
    </div>
    <my-dialog :isShow="isShowLogDialog"
               @on-close="closeDialog('isShowLogDialog')">
      <log-form @has-log="onSuccessLog"></log-form>
    </my-dialog>
    <my-dialog :isShow="isShowRedDialog"
               @on-close="closeDialog('isShowRedDialog')">
      <reg-form></reg-form>
    </my-dialog>
    <my-dialog :isShow="isShowAboutDialog"
               @on-close="closeDialog('isShowAboutDialog')">
      你好!这是我用vue写的第一个网站，请多多指导！
    </my-dialog>
  </div>
</template>

<script>
import Dialog from './base/dialog'
import LogForm from './logform'
import RegForm from './regform'
export default {
  components: {
    MyDialog: Dialog,
    LogForm,
    RegForm
  },
  data () {
    return {
      isShowLogDialog: false,
      isShowRedDialog: false,
      isShowAboutDialog: false,
      username: ''
    }
  },
  methods: {
    logDialog () {
      this.isShowLogDialog = true
    },
    regDialog () {
      this.isShowRedDialog = true
    },
    aboutDialog () {
      this.isShowAboutDialog = true
    },
    closeDialog (attr) {
      this[attr] = false
    },
    onSuccessLog (data) {
      console.log(data.username)
      this.username = data.username
      this.closeDialog('isShowLogDialog')
    },
    quit () {
      this.username = ''
    }
  }
}
</script>

<style>
body {
  margin: 0px;
  padding: 0px;
}

ul {
  padding-left: 0px;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: relative;
}

.app-header {
  height: 80px;
  background-color: #2d8cf0;
}

.app-header .logo {
  width: 50px;
  margin: 15px 0px 0px 10%;
}

.app-header .app-headerW {
  position: absolute;
  right: 10%;
  top: 10px;
}

.app-header .app-headerW li {
  list-style: none;
  float: left;
  width: 50px;
  text-align: center;
  color: #fff;
  cursor: pointer;
}

.app-header .app-headerW .nav-pile {
  width: 15px;
}

.app-header .app-headerW li.user {
  width: 150px;
}

.app-content {
  text-align: center;
  padding: 30px 0px;
  background-color: rgb(214, 214, 214);
  clear: both;
}

.app-footer {
  height: 60px;
  background-color: #333;
  clear: both;
}
</style>



