<template>
  <div class="detail">
    <div class="detail-left">
      <img src="../assets/images/1.png"
           alt="">
      <ul>
        <li v-for="(item, index) in data.boardList"
            :key="index">{{ item.title }}</li>
      </ul>
    </div>
    <div class="detail-right">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  created () {
    this.$http.post('http://localhost:8080/data')
      .then(
        function (data) {
          this.data = data.data[0].data
        }, function (err) {
          console.log(err)
        })
  },
  data () {
    return {
      data: ''
    }
  }
}
</script>

<style>
.detail {
  margin: 0px 10%;
  overflow: hidden;
}
.detail-left {
  width: 23%;
  float: left;
  text-align: center;
  background-color: #fff;
  box-shadow: 3px 3px 10px 10px rgb(214, 214, 214);
  margin-right: 2%;
  padding-top: 10px;
}
.detail-left li {
  color: #000;
  line-height: 35px;
  text-align: left;
  padding-left: 30px;
  cursor: pointer;
}
.detail-left li:hover {
  background-color: #4fc08d;
  color: #fff;
}
.detail-right {
  width: 75%;
  float: right;
  background-color: #fff;
}
</style>



