// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// eslint-disable-next-line
/* eslint-disable */
import Vue from 'vue'
// import App from './App'
// import router from './router'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import mock from '@/mock/api.js'
// import apple from '@/components/apple' 
// import banana from '@/components/banana'
import Layout from '@/components/layout'
import IndexPage from '@/pages/indexpage'
import DetailPage from '@/pages/detail'
import AnalysislPage from '@/pages/details/analysis'
import CountPage from '@/pages/details/count'
import ForecastPage from '@/pages/details/forecast'
import PublishPage from '@/pages/details/publish'

Vue.use(VueRouter)
Vue.use(VueResource)

Vue.config.productionTip = false

let router = new VueRouter({
  mode: 'hash',
  routes: [{
      path: '/',
      component: IndexPage
    },
    {
      path: '/detail',
      component: DetailPage,
      children: [{
        path: 'analsis',
        component: AnalysislPage
      }, {
        path: 'count',
        component: CountPage
      }, {
        path: 'forecast',
        component: ForecastPage
      }, {
        path: 'publish',
        component: PublishPage
      }]
    }
  ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    Layout
  },
  template: '<Layout/>'
})



