<template>
  <div class="index">
    <div class="index-left">
      <div class="index-left-top">
        <h2>全部产品</h2>
        <div v-for="product in data.productList"
             :key="product">
          <h3>{{ product.title }}</h3>
          <ul>
            <li v-for="list in product.list"
                :key="list">
              <a :href="list.url">
                {{ list.name }}
              </a>
              <span v-if="list.hot"
                    class="tag-hot">Hot</span>
            </li>
          </ul>
          <div v-if="product.last"
               class="hr"></div>
        </div>
      </div>
      <div class="index-left-bottom">
        <h2>最新消息</h2>
        <ul>
          <li v-for="item in data.newsList"
              :key="item"
              class="new-item">
            <a :href="item.url">
              {{ item.name }}
            </a></li>
        </ul>
      </div>
    </div>
    <div class="index-right">
      <div class="index-right-top">
        <slide-show :slides="slides"
                    :inv="invTime"
                    @change="dosomethingonslidechange"></slide-show>
      </div>
      <div class="index-right-bottom">
        <div class="index-right-bottom-list">
          <div class="index-right-bottom-item"
               v-for="(list,index) in data.boardList"
               :key="index"
               :class="[{'line-last' : index % 2 !== 0},'index-right-' + list.id]">
            <h4 class="title">{{ list.title }}</h4>
            <p class="describe">{{ list.description }}</p>
            <span class="btn">立即购买</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import slideShow from '../components/slideshow'
export default {
  components: {
    slideShow
  },
  // 其下渲染
  created () {
    this.$http.post('http://localhost:8080/data')
      // .then(response => {
      //   console.log(response.data)
      //   if (response.data.success) {
      //   } else {
      //   }
      // })
      .then(
        function (data) {
          this.data = data.data[0].data
        }, function (err) {
          console.log(err)
        })
    // this.datalist = Mock.data.date
  },
  data () {
    return {
      data: {},
      invTime: 2000,
      slides: [{
        src: require('../assets/slideShow/pic1.jpg'),
        title: 'xxx1',
        href: 'detail/analysis'
      }, {
        src: require('../assets/slideShow/pic2.jpg'),
        title: 'xxx2',
        href: 'detail/count'
      }, {
        src: require('../assets/slideShow/pic3.jpg'),
        title: 'xxx3',
        href: 'http://xxx.xxx.com'
      }, {
        src: require('../assets/slideShow/pic4.jpg'),
        title: 'xxx4',
        href: 'detail/forecast'
      }]
    }
  },
  methods: {
    dosomethingonslidechange () {
      console.log('dosomethingonslidechange')
    }
  }
}
</script>

<style scoped>
body {
  margin: 0px;
  padding: 0px;
}
.hr {
  border-bottom: 1px solid rgb(192, 192, 192);
}
.tag-hot {
  background: red;
  color: #fff;
  font-size: 12px;
}
.index {
  margin: 0px 10%;
  overflow: hidden;
}
.index-left {
  width: 23%;
  float: left;
  text-align: left;
  background-color: #fff;
  box-shadow: 3px 3px 10px 10px rgb(214, 214, 214);
  margin-right: 2%;
}
.index-left .hr {
  padding: 0px 10px;
}
.index-left h2 {
  background-color: #4fc08d;
  color: #fff;
  font-size: 18px;
  line-height: 45px;
  padding-left: 15px;
  margin: 0px;
}
.index-left h3 {
  padding-left: 15px;
}
.index-left a {
  color: #000;
  padding-left: 25px;
  line-height: 25px;
}
.index-left-bottom {
  min-height: 180px;
}
.new-item {
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 30px;
}
.index-right {
  width: 75%;
  float: right;
}
.index-right-bottom {
  overflow: hidden;
  width: 100%;
}
.index-right-bottom-list {
  overflow: hidden;
  width: 100%;
}
.index-right-bottom-item {
  width: 26%;
  float: left;
  min-height: 150px;
  margin-right: 2%;
  margin-bottom: 25px;
  text-align: left;
  padding-left: 20%;
  padding-right: 2%;
}
.line-last {
  margin-right: 0px;
  margin-left: 2%;
}
.index-right-bottom-item .title {
}
.index-right-bottom-item .describe {
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.index-right-bottom-item .btn {
  background-color: #4fc08d;
  color: #fff;
  padding: 5px 10px;
}
.index-right-car {
  background: #fff url(../assets/images/1.png) no-repeat 10% center;
}
.index-right-earth {
  background: #fff url(../assets/images/2.png) no-repeat 10% center;
}
.index-right-loud {
  background: #fff url(../assets/images/3.png) no-repeat 10% center;
}
.index-right-hill {
  background: #fff url(../assets/images/4.png) no-repeat 10% center;
}
</style>



