<template>
  <div class="log-from">
    <form action="">
      <ul>
        <li class="username"><label for="">用户名：</label><input type="text"
                 placeholder="请输入用户名"
                 v-model="usernameModel"><span class="error">{{ userErrors.errorText }}</span></li>
        <li class="password"><label for="">密&nbsp;&nbsp;&nbsp;&nbsp;码：</label><input type="password"
                 placeholder="请输入密码"
                 v-model="passwordModel"><span class="error">{{ passwordErrors.errorText }}</span></li>
        <li>
          <input type="button"
                 value="登录"
                 @click="onload"
                 class="onload">
        </li>
        <p class="mainError">{{ errorText }}</p>
      </ul>
    </form>
  </div>
</template>

<script type="text/javascript">
export default {
  data () {
    return {
      usernameModel: '',
      passwordModel: '',
      errorText: ''

    }
  },
  computed: {
    userErrors () {
      let errorText, status
      if (/^\w{0,3}$/g.test(this.usernameModel)) {
        status = false
        errorText = '用户名不能小于三位'
      } else {
        status = true
        errorText = ''
      }
      if (!this.userFlag) {
        errorText = ''
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.userFlag = true
      }
      return {
        status,
        errorText
      }
    },
    passwordErrors () {
      let errorText, status
      if (/^\w{0,5}$/g.test(this.passwordModel)) {
        status = false
        errorText = '密码小于六位'
      } else {
        status = true
        errorText = ''
      }
      if (!this.passwordFlag) {
        errorText = ''
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.passwordFlag = true
      }
      return {
        status,
        errorText
      }
    }
  },
  methods: {
    onload () {
      if (!this.userErrors.status || !this.passwordErrors.status) {
        this.errorText = '您输入的信息有不规范的地方'
      } else {
        this.errorText = ''
        this.$http.post('http://localhost:8080/data', [this.usernameModel, this.passwordModel])
          .then((res) => {
            alert('您的账号注册成功！')
            // eslint-disable-next-line handle-callback-err
          }, (error) => {
            alert('您的账号注册失败，请重新注册！')
          })
      }
    }
  }
}
</script>

<style scoped>
.log-from form li {
  line-height: 40px;
}
.log-from form li input {
  line-height: 23px;
}
.username {
  margin-left: 30%;
}
.password {
  margin-left: 29.9%;
}
.error {
  color: red;
  margin-left: 10px;
}
.onload {
  background-color: #4fc08d;
  color: #fff;
  border: #4fc08d;
  width: 80px;
  line-height: 28px !important;
  margin-left: 30%;
  margin-top: 20px;
}
.mainError {
  margin-left: 30%;
  color: red;
}
</style>



