<template>
  <div class="slideshow"
       @mouseover="clearInv()"
       @mouseout="runInv()">
    <div class="slides-img">
      <a :href="slides[nowindex].href">
        <transition name="slides-img-old">
          <img v-if="!isShow"
               :src="slides[nowindex].src"
               alt="">
        </transition>
        <transition name="slides-img-new">
          <img v-if="isShow"
               :src="slides[nowindex].src"
               alt="">
        </transition>
      </a>
    </div>
    <h2 class="slides-title">{{ slides[nowindex].title }}</h2>
    <ul class="slides-pages">
      <li @click="goto(previndex)">&lt;</li>
      <li v-for="(item,index) in slides"
          :key="index"
          @click="goto(index)">
        <a :class="{on: index === nowindex}">{{ index+1 }}</a>
      </li>
      <li @click="goto(nextindex)">&gt;</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'slidesshow',
  props: {
    slides: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    },
    inv: {
      type: Number,
      default: 1000
    }
  },
  data () {
    return {
      nowindex: 0,
      isShow: true
    }
  },
  computed: {
    previndex () {
      if (this.nowindex === 0) {
        return this.slides.length - 1
      } else {
        return this.nowindex - 1
      }
    },
    nextindex () {
      if (this.nowindex === this.slides.length - 1) {
        return 0
      } else {
        return this.nowindex + 1
      }
    }
  },
  methods: {
    goto (index) {
      this.isShow = false
      setTimeout(() => {
        this.isShow = true
        this.nowindex = index
        this.$emit('change', index)
      }, 10)
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextindex)
      }, this.inv)
    },
    clearInv () {
      clearInterval(this.invId)
    }
  },
  mounted () {
    this.runInv()
  }
}
</script>

<style scoped>
body {
  margin: 0px;
  padding: 0px;
}
.slideshow {
  position: relative;
  color: #fff;
  margin-bottom: 25px;
  height: 300px;
  overflow: hidden;
}
.slides-img {
  width: 100%;
}
.slides-img img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.slideshow::after {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 30px;
  background-color: rgba(00, 00, 00, 0.6);
  z-index: 3;
}
.slides-title {
  position: absolute;
  bottom: 0px;
  left: 15px;
  margin-bottom: 0px;
  z-index: 5;
  line-height: 30px;
  font-size: 16px;
}
.slides-pages {
  position: absolute;
  right: 10px;
  bottom: 0px;
  overflow: hidden;
  margin-bottom: 0px;
  z-index: 5;
}
.slides-pages li {
  float: left;
  width: 30px;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
}
.slides-pages li .on {
  color: red;
}
.slides-img-new-enter {
  transform: translateX(900px);
}
.slides-img-new-enter-active {
  transition: all 0.5s;
}
.slides-img-old-leave-active {
  transition: all 0.5s;
  transform: translateX(-900px);
}
</style>



